.projects {
    display: flex;
    flex-direction: column;
    padding-bottom: 5rem;
    background-color: hsl(232, 50%, 97%);
    color: hsl(225, 29%, 23%);
  }
  
  .project {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100%;
    min-height: 100dvh;
    padding: 0 1rem;
    position: relative;
    overflow: hidden;
  }
  
  .project *::selection {
    background-color: hsla(168, 100%, 33%, 0.2);
    color: hsl(224, 29%, 15%);
  }
  
  .container {
    width: 100%;
    height: 100%;
    max-width: 1200px;
    margin: 0 auto;
    display: flex;
    flex-wrap: nowrap;
    position: relative;
  }
  
  .static {
    width: 100%;
    max-width: 600px;
    height: 100%;
    min-height: 100dvh;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding-bottom: 4rem;
  }
  
  .top {
    position: relative;
    display: flex;
    flex-direction: column;
    gap: 1rem;
    padding: 0 1rem;
    padding-top: 4rem;
  }
  
  .bottom {
    position: relative;
    display: flex;
    gap: 1rem;
    padding: 0 1rem;
  }
  
  .projectNumber {
    position: absolute;
    top: 0;
    left: 0;
    font-size: 1.5rem;
    font-weight: 700;
    padding: 1rem;
    color: hsl(225, 29%, 23%);
    overflow: hidden; /* for animation */
    transition: opacity 0.2s ease-in-out; /* for animation */
  }
  
  .title {
    font-size: 3rem;
    font-weight: 700;
    letter-spacing: 0.1rem;
    text-transform: uppercase;
    font-family: sans-serif;
    margin: 0;
    margin-top: 2rem;
    color: hsl(225, 29%, 23%);
    position: relative; /* for animation */
    overflow: hidden; /* for animation */
    transition: opacity 0.2s ease-in-out; /* for animation */
  }
  
  .subtitle {
    font-size: 1.2rem;
    font-weight: 500;
    letter-spacing: 0.1rem;
    line-height: 1.6;
    margin: 0;
    margin-top: 1.5rem;
    text-transform: uppercase;
    color: hsl(161, 86%, 44%);
    position: relative; /* for animation */
    overflow: hidden; /* for animation */
    transition: opacity 0.2s ease-in-out; /* for animation */
  }
  
  .description {
    font-size: 1.5rem;
    font-weight: 400;
    letter-spacing: 0.05rem;
    line-height: 1.6;
    margin: 0;
    margin-top: 0.5rem;
    color: hsl(225, 29%, 30%);
    position: relative; /* for animation */
    overflow: hidden; /* for animation */
    transition: opacity 0.2s ease-in-out; /* for animation */
  }
  
  .bottom {
    display: flex;
    align-items: center;
    width: min-content;
    gap: 1.5rem;
    padding: 0 1rem;
    position: relative; /* for animation */
    transition: opacity 0.2s ease-in-out; /* for animation */
  }
  
  .bottom .link {
    font-size: 1rem;
    font-weight: 500;
    letter-spacing: 0.05rem;
    margin: 0;
    color: hsl(224, 29%, 15%);
    text-decoration: none;
    transition: transform 0.2s ease-in-out;
    position: relative; /* for animation */
    cursor: pointer;
    overflow: hidden; /* for animation */
    z-index: 5;
  }
  
  .link svg {
    width: 1.5rem;
    height: 1.5rem;
    fill: hsl(224, 29%, 15%);
    transition: fill 0.2s ease-in-out;
  }
  
  .link:hover {
    transform: scale(1.2);
  }
  
  .dynamic {
    width: 100%;
    height: 100%;
    min-height: 100dvh;
    position: relative;
  }
  
  .imageContainer {
    flex-shrink: 0;
    max-height: 90dvh;
    max-width: 400px;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    transition: transform 0.2s ease-in-out;
    z-index: 0;
  }
  
  .webImageContainer {
    flex-shrink: 0;
    max-height: 90dvh;
    max-width: 600px;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -60%);
    transition: transform 0.2s ease-in-out;
    z-index: 0;
  }
  
  .imageContainer img,
  .webImageContainer img {
    width: 100%;
    height: 100%;
    object-fit: contain;
    object-position: center;
    border-radius: 0.2rem;
    -webkit-user-select: none;
    -webkit-user-drag: none;
    user-select: none;
  }
  
  .asideImage {
    transform: scale(0.8) translate(-20%, -50%);
    pointer-events: none;
  }
  
  .imageContainer:hover {
    z-index: 1;
    transform: translate(-50%, -50%) rotate(-10deg);
  }
  
  .imageContainer:hover + .asideImage {
    transform: scale(0.8) translate(-20%, -50%) rotate(10deg);
  }
  
  .webImageContainer:hover {
    transform: scale(1.1) translate(-50%, -60%);
  }
  
  @media screen and (max-width: 820px) {
    .container {
      flex-direction: column;
    }
  
    .static {
      max-width: 100%;
      height: 100%;
      min-height: unset;
      padding-bottom: 2rem;
      gap: 2rem;
    }
  
    .dynamic {
      height: 100%;
      min-height: 60dvh;
    }
  
    .dynamic .imageContainer {
      left: 37%;
      top: 50%;
    }
  
    .dynamic:has(.webImageContainer) .imageContainer:not(.webImageContainer) {
      transform: scale(0.7) translate(0%, -60%);
    }
  }