.animate_left::after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 200%;
    height: 100%;
    background: linear-gradient(
      to left,
      hsl(224, 47%, 97%) 0%,
      hsl(231, 47%, 97%) 50%,
      hsl(224, 29%, 15%) 50%,
      hsl(231, 29%, 15%) 100%
    );
    z-index: 1;
    opacity: 0;
    animation: disclose-left 0.5s cubic-bezier(0.19, 0.85, 0.71, 0.15);
  }
  
  @keyframes disclose-left {
    0% {
      left: -100%;
      opacity: 1;
    }
    100% {
      left: 100%;
      opacity: 1;
    }
  }
  
